.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  margin-bottom: 60px;
  line-height: normal;
  text-align: center;
  font-size: 13px;
  align-items: center;
  display: flex;
  justify-content: center;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-bottom: 40px;
  }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  width: 40px;
  padding: 12px 0px;

  cursor: pointer;

  color: rgb(255, 255, 255);
  line-height: 1;
  text-decoration: none;

  color: rgb(67, 67, 67);
  border-radius: 5px;
}

.tui-page-btn:not(:first-child) {
  margin-left: 5px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  cursor: pointer;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 28px;
  padding: 0;
  font-size: 0;
  line-height: 28px;
  white-space: nowrap;
  font-weight: normal;
  // border: 1px solid #ddd;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;

  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}
.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}
.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}
.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}
.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 14px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-is-disabled {
  // background-color: #f7f7f7;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  // background-position: -26px -12px;
}
.tui-pagination .tui-is-disabled .tui-ico-first {
  // background-position: -13px 0px;
}
.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}
.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}
.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}
.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 9px 0 6px;
  font-size: 12px;
  text-align: center;
  color: rgb(255, 255, 255);
  border: 0;
  outline: none;
}
.tui-page-inputs .tui-page-current {
  color: #e67e4b;
}
.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
.tui-page-btn.tui-last,
.tui-page-btn.tui-first,
.tui-page-btn.tui-next-is-ellip.tui-last-child,
.tui-page-btn.tui-prev-is-ellip.tui-first-child {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: none;
  }
}
.tui-page-btn.tui-is-disabled.tui-prev,
.tui-page-btn.tui-is-selected.tui-first-child,
.tui-page-btn.tui-next {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-left: 0px;
  }
}
