}

.tvdd__ring-dots {
  width: 100%;
  height: 100%;
  animation: 1s linear infinite pivot;
}

.tvdd__ring-dot {
  width: 10%;
  height: 10%;
  background-color: #f80;
  border-radius: 50%;
  margin: -5% 0 0 -5%;
  animation: 1s ease-in-out infinite pulse;
}

.tvdd__ring-dot:nth-child(1) {
  top: 38.3531%;
  left: 6.53334%;
}

.tvdd__ring-dot:nth-child(2) {
