  animation-delay: -.166667s;
  top: 81.8198%;
  left: 18.1802%;
}

.tvdd__ring-dot:nth-child(4) {
  animation-delay: -.25s;
  top: 93.4667%;
  left: 38.3531%;
}

.tvdd__ring-dot:nth-child(5) {
  animation-delay: -.333333s;
  top: 93.4667%;
  left: 61.6469%;
}

.tvdd__ring-dot:nth-child(6) {
  animation-delay: -.416667s;
  top: 81.8198%;
  left: 81.8198%;
}

.tvdd__ring-dot:nth-child(7) {
  animation-delay: -.5s;
  top: 61.6469%;
  left: 93.4667%;
}

.tvdd__ring-dot:nth-child(8) {
  animation-delay: -.583333s;
  top: 38.3531%;
  left: 93.4667%;
}

.tvdd__ring-dot:nth-child(9) {
  animation-delay: -.666667s;
  top: 18.1802%;
