$white-color: #ffff;
$red-color: #ff001b;
$black-color: #000000;
$orange-color: #ff6b08;
$gray-color: #8c8c8c;
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html {
  background-color: #f7f7f7;
  scroll-behavior: smooth;
}
body {
  font-family: roboto;

  background-color: white;
}
main {
  background-color: white;
}
button {
  cursor: pointer;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
p,
h1,
h2 {
  margin: 0px;
}
a {
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
}
ul {
  list-style: none;
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 32px;
  padding-right: 32px;
  width: 1280px;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 320px;
    padding: 0px 20px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 768px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}
.hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0px;
  padding: 0px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.center {
  display: flex;
  align-items: center;
}
